<template>
<div>
    <div class="register_con">
		<div class="l_con fl">
			<a href="../templates/index.html" class="reg_logo"><img src="../assets/images/logo.png"></a>
			<div class="reg_slogan">商品美 · 种类多 · 欢迎光临</div>
			<div class="reg_banner"></div>
		</div>
		<div class="r_con fr">
			<div class="reg_title clearfix">
				<h1>用户注册</h1>
				<a href="../templates/login.html">登录</a>
			</div>
			<div class="reg_form clearfix">
				<form method="post" class="register_form"  @click = 'on_submit'>
				<ul>
					<li v-for = "(item,index) in lists">
						<label>{{item.label}}:</label>
						<input type="text" :name="item.name" :id="item.id" :class='item.class' @blur="
						index ==0 && check_username(index) ||
						index ==1 && check_pwd(index) ||
						index ==2 && check_cpwd(index) ||
						index ==3 && check_phone(index) 

						"
						v-model='item.val'>
						<img src="../assets/images/pic_code.jpg" alt="图形验证码" class="pic_code" v-if='index==4'>
						<a href="javascript:;" class="get_msg_code" v-if='index==5'>获取短信验证码</a>
                        <span class = 'error_tip' v-show="item.error_show">{{item.placehoder}}</span>
					</li>					
					<li class="agreement">
						<input type="checkbox" name="allow" id="allow" checked="checked" >
						<label>同意”美多商城用户使用协议“</label>
					</li>
					<li class="reg_sub">
						<input type="submit" value="注 册">
					</li>

				</ul>				
				</form>
			</div>

		</div>

	</div>

    	<div class="footer no-mp">
		<div class="foot_link">
			<a href="#">关于我们</a>
			<span>|</span>
			<a href="#">联系我们</a>
			<span>|</span>
			<a href="#">招聘人才</a>
			<span>|</span>
			<a href="#">友情链接</a>		
		</div>
		<p>CopyRight © 2016 北京美多商业股份有限公司 All Rights Reserved</p>
		<p>电话：010-****888    京ICP备*******8号</p>
	</div>
	</div>
</div>



</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      lists:[
          {'label' :'用户名','name':  'user_name' ,'id':'user_name' ,'placehoder':'请输入5-20个字符的用户','error_show':false,val:''},
          {'label' :'密码','name':  'pwd' ,'id':'pwd' ,'placehoder':'请输入8-20位的密码','error_show':false,val:''},
          {'label' :'确认密码','name':  'cpwd' ,'id':'cpwd' ,'placehoder':'两次输入的密码不一致','error_show':false,val:''},
          {'label' :'手机号','name':  'phone' ,'id':'phone' ,'placehoder':'请输入正确的手机号码','error_show':false,val:''},
          {'label' :'图形验证码','name':  'pic_code' ,'id':'pic_code' ,'placehoder':'请填写图形验证码','class':'msg_input','error_show':false,val:''},
          {'label' :'短信验证码','name':  'msg_code' ,'id':'msg_code' ,'placehoder':'请填写短信验证码','class':'msg_input','error_show':false,val:''},
      ],

		image_code_id: '',
		image_code_url: '',
		sms_code_tip: '获取短信验证码',
		sending_flag: false,
		username: '',
		password: '',
		password2: '',
		mobile: '',
		image_code: '',
		sms_code: '',
		allow: true
    }
  },
  methods: {
	  		// 检查用户名
		check_username: function (index){
			var that = this
			var re = /^[a-zA-Z0-9_-]{5,20}$/;
			if (re.test(this.lists[index].val)) {
				this.lists[index].error_show = false;
			} else {
				this.lists[index].placehoder = '请输入5-20个字符的用户名';
				this.lists[index].error_show = true;
			}
			// 检查重名
			if (this.lists[index].error_show == false) {
				var url = '/usernames/' + this.lists[index].val + '/count/';
				this.$get(url,{
						responseType: 'json'
					})
					.then(response => {
						if (response.count > 0) {
							that.lists[index].placehoder ='用户名已存在' 
							that.lists[index].error_show = true;
						} else {
							that.lists[index].error_show = false;
						}
					})
					.catch(error => {
						console.log(error.response);
					})
			}
		},
			// 检查密码
		check_pwd: function (index){
			var re = /^[0-9A-Za-z]{8,20}$/;
			if (re.test(this.lists[index].val)) {
				this.lists[index].error_show = false;
			} else {
				this.lists[index].error_show = true;
			}
		},
		// 确认密码
		check_cpwd: function (index){
			if(this.lists[index-1].val!=this.lists[index].val) {
				this.lists[index].error_show = true;
			} else {
				this.lists[index].error_show = false;
			}
		},
				// 检查手机号
		check_phone: function (index){
			var re = /^1[345789]\d{9}$/;
			if(re.test(this.lists[index].val)) {
				this.lists[index].error_show = false;
			} else {
				this.lists[index].placehoder = '您输入的手机号格式不正确';
				this.lists[index].error_show = true;
			}
			if (this.lists[index].error_show == false) {
				var url =  '/mobiles/'+ this.lists[index].val + '/count/';
				this.$get(url, {
						responseType: 'json'
					})
					.then(response => {
						if (response.count > 0) {
							this.lists[index].placehoder = '手机号已存在';
							this.lists[index].error_show = true;
						} else {
							this.lists[index].error_show = false;
						}
					})
					.catch(error => {
						console.log(error.response);
					})
			}
		},
			// 检查图片验证码
		check_image_code: function (index){
			if(!this.lists[index].val) {
				this.lists[index].placehoder = '请填写图片验证码';
				this.lists[index].error_show = true;
			} else {
				this.lists[index].error_show = false;
			}
		},
		// 检查短信验证码
		check_sms_code: function(index){
			if(!this.lists[index].val){
				this.lists[index].placehoder = '请填写短信验证码';
				this.lists[index].error_show = true;
			} else {
				this.lists[index].error_show = false;
			}
		},
		on_submit:function(){
			window.event.returnValue = false
			 this.check_username(0) 
			 this.check_pwd(1) 
			 this.check_cpwd(2) 
			 this.check_phone(3) 
			 this.check_image_code(4)
			 this.check_sms_code(5)
			 if(this.lists[0].error_show == true || this.lists[1].error_show == true  || this.lists[2].error_show == true 
				|| this.lists[3].error_show == true || this.lists[4].error_show == true  || this.lists[5].error_show == true ) {
                // 不满足注册条件：禁用表单
				return false;
            }
			this.$post('/register',{
				'username':this.lists[0].val,
        		'password':this.lists[1].val,
        		'password2':this.lists[2].val,
        		'phone':this.lists[3].val,
        		'allow':'on',
			})
  		},

}
}
</script>

